import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MenuController } from '@ionic/angular';
import { AuthService } from 'src/app/core/auth.service';
import { LanguageService } from 'src/app/core/language.service';
import { MarketService } from 'src/app/core/market.service';
import { UtilityService } from 'src/app/core/utility.service';
import { Config } from 'src/app/shared/accordion/types';
import { Menu } from 'src/app/shared/accordion/types';
import { cat } from '../cat-breed';
import { dog } from '../dog-breed';

@Component({
  selector: 'app-signup-pet-form',
  templateUrl: './signup-pet-form.component.html',
  styleUrls: ['./signup-pet-form.component.scss'],
})
export class SignupPetFormComponent implements OnInit {
  options: Config = { multi: false };
  registerForm: FormGroup;
  petTypes;
  dogBreed = dog;
  catBreed = cat;
  menus: Menu[] = [
    {
      name: 'Vaccine',
      iconClass: 'fa fa-code',
      active: true,
      submenu: [
        { name: 'HTML', url: '#' },
        { name: 'CSS', url: '#' },
        { name: 'Javascript', url: '#' },
      ],
    },
    {
      name: 'Treatment',
      iconClass: 'fa fa-mobile',
      active: false,
      submenu: [
        { name: 'Tablets', url: '#' },
        { name: 'Mobiles', url: '#' },
        { name: 'Desktop', url: '#' },
      ],
    },
  ];
  constructor(
    private authService: AuthService,
    private utility: UtilityService,
    private languageService: LanguageService,
    private fb: FormBuilder,
    private menuCtrl: MenuController,
    private marketService: MarketService
  ) {}

  ngOnInit() {
    this.buildForm();
    this.getPetTypes();
  }

  buildForm() {
    this.registerForm = this.fb.group({
      numberOfPet: ['', [Validators.required]],
      petType: this.fb.array([]),
    });
  }

  get petType() {
    return this.registerForm.get('petType') as FormArray;
  }
  // pattern=""

  get f() {
    return this.registerForm.controls;
  }

  // get petBreed() {
  //   return this.registerForm.get('petBreed') as FormArray;
  // }

  // addBreed() {
  //   const pet = this.fb.group({
  //     petBreed: this.fb.array([]),
  //   });
  //   this.petBreed.push(new FormControl(''));
  // }

  removePetType(i) {
    this.petType.removeAt(i);
  }

  addPetType() {
    const pet = this.fb.group({
      petType: ['', [Validators.required]],
      petBreed: [''],
    });
    this.petType.push(pet);
  }

  getPetTypes() {
    this.marketService.getPetType().subscribe((res) => {
      this.petTypes = res;
    });
  }

  handleChange(e) {
    console.log(e);
    const val = e.detail.value;
    this.petType.clear();
    for (let i = 1; i <= val; i++) {
      this.addPetType();
    }
  }
  getPetValue(i) {
    return this.registerForm.value.petType[i].petType.toLowerCase() == 'dog';
  }

  handlePetTypeChange(e) {
    console.log(e);
    console.log(this.registerForm.value.petType);
    this.petType.controls[0].get('petBreed').setValue('');
  }

  submit(form) {
    console.log(form);
    if (form.status.toLowerCase() === 'valid') {
      const formObject = form.value;
      formObject.role = 'pet owner';

      delete formObject.cPassword;
      // formObject.petType = formObject.petType.map((v) => v.petType);
      console.log(formObject);
      this.utility.present();
      this.authService.register(formObject).subscribe(
        (res: any) => {
          console.log(res);
          this.utility.setLocal(
            'cred',
            window.btoa(
              JSON.stringify(formObject.email + ':' + formObject.password)
            )
          );
          if (res['status'] == true) {
            this.utility.setLocal('userObj', JSON.stringify(res.data));

            let json = {
              email: formObject.email,
              password: formObject.password,
              role: 'pet owner',
            };

            this.authService.login(json).subscribe((resp) => {
              console.log(resp);
              const token = resp['token'];
              const userObj = resp['data'];
              this.utility.setLocal('token', token);
              this.utility.setLocal('userObj', JSON.stringify(userObj));
              this.utility.setSideMenuUserDetail();

              this.utility.dismiss();

              this.utility.navigate('/subscription');
            });
            // this.utility.navigate('/login');
          } else {
            // this.utility.showAlert(res['message']);
            this.utility.dismiss();

            this.utility.presentToastWithOptions(res['message']);
          }
        },
        (err) => {
          this.utility.dismiss();
          // this.utility.hideLoading();
        }
      );
    }
  }
}
